walking-display {
    display: inline-flex;
    align-items: center;
    position: relative;
    padding-left: 0.8em;
    height: 1em;
    color: var(--timeline--walking);
     .material-icons {
        position: absolute;
        left: -0.2em;
        top:-0.15em;
        font-size:1.2em;
    }
}

.walking-display {
    &__minutes {
        font-size: 0.8em;
    }
}